<template>
    <div class="spread">
        <!--<img  src="../../assets/spread.png" class="img-responsive">-->
        <a href="javascript:;" class="generate" @click="generate" data-clipboard-text=""></a>
    </div>
</template>

<script>   
    import Clipboard from "clipboard" ; 
    import $ from "jquery" ;
    import "@/libraries/jquery.modal" ;    
    export default {
        name : "spread" ,
        data () {
            return {
                
            }
        } , 
        props : [ "title" , "articleId" ] ,
        methods : {
            generate() {
                let clipboardContent = { "url" : encodeURIComponent(window.location.href.replace( "share" , "hybrid" )) , "title" : this.title , "articleId" : this.articleId } ;
                //data-clipboard-text
                $(".generate").attr("data-clipboard-text" , JSON.stringify(clipboardContent)) ;     
                let clipboard = new Clipboard(".generate") ;
                clipboard.on("success" , function(e) {                                 
                    $.modal({
                        "id" : "spreadSuccessDialog" ,
                        "title" : "" ,
                        "content" : "~" ,                        
                        "buttons" : [
                            { "text" : "" , "clickInterface" : function(){ $.modal.close("spreadSuccessDialog") ; } }                             
                        ]
                    }) ;
                }) ;
                clipboard.on("error" , function(e) {
                   $.modal({
                        "id" : "spreadErrorDialog" ,
                        "title" : "" ,
                        "content" : "<p>~</p><br><p style=\"word-wrap:break-word\">" + JSON.stringify(clipboardContent) + "</p>" ,
                        "buttons" : [
                            { "text" : "" , "clickInterface" : function(){ $.modal.close("spreadErrorDialog") ; } }                             
                        ]
                    }) ;
                }) ;
            }
         }            
    }
</script>
<style lang="less">
       @import "../../../src/less/components/spread.less" ;
</style>


